































































































import Vue from 'vue'
import axios from 'axios'
export default Vue.extend({
  data() {
    return {
      beer: {},
    }
  },
  mounted() {
    this.fetchBeer()
  },
  methods: {
    async fetchBeer() {
      try {
        const response = await axios.get(`https://api.punkapi.com/v2/beers/${this.$route.params.id}`)
        this.beer = response.data[0]
      } catch(error) {
        console.log(error);
        
      }
    }
  }
})
